.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    background-color: bisque;
    margin: 0px;
    padding-left: 30px;
    padding-right: 30px;
}
.leftBar{
    display: flex;
    align-items: center;
    gap: 3px;
}
.leftBar span{
    text-align: center;   
    font-size: xx-large;
    cursor: pointer;
}
.leftBar img{
    height: 50px;
    width: 70px;
    border-radius: 50%;
    cursor: pointer;
}
.rightBar ul{
    list-style: none;
}
.rightBar ul li{
    display: inline-flex;
    padding: 10px;        
}
.rightBar ul li:hover{
    background-color: rgb(94, 136, 94);
    cursor: pointer;  
    color: white;      
}
.dashboard {
    display: flex;
    gap: 10px;
    margin: 30px;
}
.sidebar {
    flex: 1;
    border: 1px solid black;
    padding: 20px;
    border-radius: 3px;
}
.main {
    flex: 4;
    border: 1px solid black;
    border-radius: 3px;
    padding: 5px;
}